.accessibility-aid {
    height: 1px;
    width: 1px;
    clip: rect(1px, 1px, 1px, 1px);
    overflow: hidden;
    position: absolute;
    margin: 0
}

.accessibility-aid:focus {
    height: auto;
    width: auto;
    clip: auto;
    z-index: 0;
    top: 0;
    line-height: 40px;
    padding: 0 10px;
    background: #f5f5f5;
    font-weight: bold;
    text-decoration: none;
    color: #333
}

.is-stats .accessibility-aid:focus {
    top: 34px
}


.screen-reader-text { /* Reusable, toolbox kind of class */
  position: absolute;
  top: -9999px;
  left: -9999px;
}